
.validation {
  background: transparent;
  font-size: 14px;
}
.validation .day {
    white-space: nowrap;
}
.validation .day .icon-less, .validation .day .icon-exact, .validation .day .icon-many {
      display: none;
}
.validation .day.exact {
      color: darkgreen;
}
.validation .day.less {
      color: darkgreen;
}
.validation .day.less .icon-less {
        display: inline-block;
        color: green;
}
.validation .day.less.warnmin {
        background: red;
}
.validation .day.less.warnmin strong {
          color: white;
}
.validation .day.less.warnmin .icon-less {
          color: white;
}
.validation .day.many {
      color: darkgreen;
}
.validation .day.many .icon-many {
        display: inline-block;
        color: green;
}
.validation .day.many.warnmax {
        background: red;
}
.validation .day.many.warnmax strong {
          color: white;
}
.validation .day.many.warnmax .icon-many {
          color: white;
}
.validation table.table td, .validation table.table th {
    padding: 2px;
    font-size: .9em;
}
.validation table.table .datas > th {
    padding-left: 1em;
}
.validation table.table .heading-activity > th {
    padding-top: 1em;
    border-top: solid 1px #ddd;
    font-weight: 100;
}
.validation .has-validation {
    cursor: pointer;
    border-left: 4px #0b93d5 solid;
}
.validation .heading {
    font-size: 1.2em;
}
.validation th small {
    display: block;
}
.validation tbody tr.datas:hover {
    background: rgba(92, 156, 204, 0.25);
}
.validation .heading-days th {
    text-align: center;
}
.validation .heading-days th.locked {
      background: #CCC;
}
.validation td, .validation th {
    border-right: thin #ddd solid;
}
.validation th small {
    font-weight: 100;
    font-size: .8em;
}
.validation td:nth-child(odd) {
    background: rgba(204, 204, 204, 0.3);
}
.validation .datas th {
    padding-left: 1em;
}
.validation .datas td, .validation .total-row td {
    text-align: right;
}
.validation .total {
    text-align: right;
    font-size: 1.2em;
}



